function sum(a, b) {
  return a + b;
}

// named export
// ter vrios exports dentro de um msm arquivo
// s pode chamar com o msm nome
// import precisa das chaves { sub }
export function sub(a, b) {
  return a - b;
}

function mult(a, b) {
  return a * b;
}

function div(a, b) {
  return a / b;
}

const PI = 3.14

export { mult as multiplicacao, div, PI };

// mtodo principal
// s pode ter um default por arquivo
// importar com qualquer nome
// no precisa utilizar as chaves
export default sum;